<template><undefined>
<h1>Cascader 级联选择</h1>
<p>级联选择框。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>需要从一组相关联的数据集合进行选择，例如省市区，公司层级，事物分类等。</li>
<li>从一个较大的数据集合中进行选择时，用多级分类进行分隔，方便选择。</li>
<li>比起 Select 组件，可以在同一个浮层中完成选择，有较好的体验。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-cascader :data=\&quot;options\&quot; @change=\&quot;onchange\&quot;&gt;&lt;/v-cascader&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      options: [{\n          value: 'sc',\n          label: '四川',\n          children: [{\n              value: 'cd',\n              label: '成都',\n              children: [{\n                  value: 'jyq',\n                  label: '九眼桥'\n              }, {\n                  value: 'cxl',\n                  label: '春熙路'\n              }],\n          }, {\n              value: 'nc',\n              label: '南充',\n              children: [{\n                  value: 'nbx',\n                  label: '南部县'\n              }]\n          }],\n      }, {\n          value: 'cq',\n          label: '重庆',\n          children: [{\n              value: 'lp',\n              label: '梁平',\n              children: [{\n                  value: 'sgt',\n                  label: '双桂堂'\n              }],\n          }],\n      }]\n    }),\n    methods: {\n      onchange(val) {\n        console.log(val)\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-cascader :data="options" @change="onchange"></v-cascader>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>省市区级联。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-cascader</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onchange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-cascader</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">options</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'sc'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'四川'</span>,
          <span class="hljs-attr">children</span>: [{
              <span class="hljs-attr">value</span>: <span class="hljs-string">'cd'</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'成都'</span>,
              <span class="hljs-attr">children</span>: [{
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'jyq'</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">'九眼桥'</span>
              }, {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'cxl'</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">'春熙路'</span>
              }],
          }, {
              <span class="hljs-attr">value</span>: <span class="hljs-string">'nc'</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'南充'</span>,
              <span class="hljs-attr">children</span>: [{
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'nbx'</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">'南部县'</span>
              }]
          }],
      }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'cq'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'重庆'</span>,
          <span class="hljs-attr">children</span>: [{
              <span class="hljs-attr">value</span>: <span class="hljs-string">'lp'</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'梁平'</span>,
              <span class="hljs-attr">children</span>: [{
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'sgt'</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">'双桂堂'</span>
              }],
          }],
      }]
    }),
    <span class="hljs-attr">methods</span>: {
      onchange(val) {
        <span class="hljs-built_in">console</span>.log(val)
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
