<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="cascader-props"><a class="header-anchor" href="#cascader-props" aria-hidden="true">¶</a> Cascader Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定选中项</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>data</td>
<td>可选项数据源</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位文本</td>
<td>string</td>
<td>请选择</td>
</tr>
<tr>
<td>size</td>
<td>输入框大小，可选 large default small</td>
<td>string</td>
<td>default</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>allowClear</td>
<td>是否支持清除</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>position</td>
<td>下拉框的定位方式（absolute,fixed）</td>
<td>string</td>
<td>absolute</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>function</td>
<td>() =&gt; document.body</td>
</tr>
</tbody>
</table>
<h3 id="cascader-events"><a class="header-anchor" href="#cascader-events" aria-hidden="true">¶</a> Cascader Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>value改变时触发</td>
<td>value</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
  export default {
    data: ()=> ({
      value: ['cq','lp','sgt'],
      options: [{
          value: 'sc',
          label: '四川',
          children: [{
              value: 'cd',
              label: '成都',
              children: [{
                  value: 'jyq',
                  label: '九眼桥'
              }, {
                  value: 'cxl',
                  label: '春熙路'
              }],
          }, {
              value: 'nc',
              label: '南充',
              children: [{
                  value: 'nbx',
                  label: '南部县'
              }]
          }],
      }, {
          value: 'cq',
          label: '重庆',
          children: [{
              value: 'lp',
              label: '梁平',
              children: [{
                  value: 'sgt',
                  label: '双桂堂'
              }],
          }],
      }],
      options2: []
    }),
    watch: {
      'value'(val){
        console.log(val)
      }
    },
    mounted(){
      let opt = JSON.parse(JSON.stringify(this.options))
      opt[0].disabled = true;
      this.options2 = opt;
    },
    methods: {
      onchange(val) {
        console.log(val)
      }
    }
  }
</script>


